body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px; /* 버튼 간 간격 증가 */
}

.btn {
  padding: 16px 32px; /* 버튼 크기 증가 */
  font-size: 20px; /* 버튼 글꼴 크기 증가 */
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s, box-shadow 0.3s;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3); /* 입체감 있는 그림자 */
}

.btn:hover {
  background-color: #0056b3;
  box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.3); /* 호버 시 더 강한 입체감 */
}

/* 반응형 디자인을 위한 미디어 쿼리 */

/* 작은 화면 (모바일) */
@media (max-width: 600px) {
  .btn {
    font-size: 18px; /* 모바일 화면에서도 글꼴 크기 증가 */
    padding: 14px 28px; /* 모바일 화면에서도 버튼 크기 증가 */
  }
}

/* 중간 화면 (태블릿) */
@media (min-width: 601px) and (max-width: 1024px) {
  .btn {
    font-size: 22px; /* 태블릿 화면에서 글꼴 크기 증가 */
    padding: 18px 36px; /* 태블릿 화면에서 버튼 크기 증가 */
  }
}

/* 큰 화면 (PC) */
@media (min-width: 1025px) {
  .btn {
    font-size: 24px; /* PC 화면에서 글꼴 크기 증가 */
    padding: 20px 40px; /* PC 화면에서 버튼 크기 증가 */
  }
}
